.popup {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    transition: all .2s linear;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
}